h2,p{
    color: rgb(28, 6, 11);
    padding : 10px

}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full viewport height */
  }
  
  .card {
    height: 25vh;
    background-color: white;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
.button-container {
    margin-top: 20px;
}
.button {
    padding: 10px 20px;
    margin: 5px;
    border: none;
    border-radius: 6px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    width: 120px; 
    text-align: center; 
}
.button:hover {
    background-color: #0056b3;
}