/* Container */
.container {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Arial, sans-serif;
    
  }
  body {
    margin :0;
    overflow: hidden; 
    background-color: #f0f8ff;
}


  /* Login Box */
  .loginBox {
    background-color: #ffffff; /* White background */
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    width: 100%;
    max-width: 400px;
    text-align: center;
  }
  
  /* Title */
  .title {
    font-size: 2rem;
    font-weight: bold;
    color: #1e90ff; /* Blue color */
    margin-bottom: 20px;
  }
  
  /* Form Styling */
  .form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  /* Form Group */
  .formGroup {
    text-align: left;
  }
  
  /* Label */
  .label {
    font-size: 0.9rem;
    color: #333333;
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
  }
  
  /* Input Fields */
  .input {
    width: 100%;
    padding: 10px;
    border: 1px solid #dcdcdc; /* Light gray border */
    border-radius: 4px;
    font-size: 1rem;
    background-color: #f9f9f9;
    transition: border 0.2s ease-in-out;
  }
  
  .input:focus {
    border-color: #1e90ff; /* Blue border on focus */
    outline: none;
  }
  
  /* Button */
  .button {
    background-color: #1e90ff; /* Blue background */
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #187bcd; /* Darker blue on hover */
  }
  
  /* Signup Text */
  .signupText {
    font-size: 0.9rem;
    color: #333333;
    margin-top: 15px;
  }
  
  .signupText .link {
    color: #1e90ff;
    text-decoration: none;
  }
  
  .signupText .link:hover {
    text-decoration: underline;
  }
  .error {
    color: #d9534f; 
    font-size: 0.9rem; 
    font-weight: bold; 
    margin-top: 0.3rem; 
    margin-bottom: 0.5rem; 
  }
  