
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Inline:opsz,wght@10..72,100..900&display=swap');
.container {
    max-width: 500px;
    margin: 0 auto;
    padding: 40px;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 10px 10px 10px;
    
    
}

.signupForm {
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
}

.formGroup {
    margin-bottom: 15px;
    padding : 10px;
    
    
}

.formGroup label {
    font-family: 'Lato', sans-serif;
    font-size: 0.9rem;
    color: #333333;
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
    margin-bottom: 5px;
}

.formGroup input,
.formGroup select {
    font-family: 'Lato', sans-serif;
    width: 100%;
    box-sizing: border-box;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.button {
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    grid-column: span 2;
    margin-left: 10px;
    margin-right:10px;
    
}

.button:hover {
    background-color: #0056b3;
}

button:disabled {
    background-color: #cccccc; 
    color: #666666; 
    border: 1px solid #aaaaaa; 
    cursor: not-allowed; 
    opacity: 0.6; 
  }

  
button:disabled:hover {
    background-color: #cccccc; 
}
.styledhr {
    border: none;
    height: 1px;
    background-color: #e0e0e0;
    margin: 30px 0;
    width: 80%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}
h2{
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    color: #1e90ff; /* Blue color */
    margin-bottom: 20px;
}
.error {
    color: #d9534f; 
    font-size: 0.9rem; 
    font-weight: bold; 
    margin-top: 0.3rem; 
    margin-bottom: 0.5rem; 
  }

  .signupText {
    font-size: 0.9rem;
    color: #333333;
    margin-top: 15px;
     text-align: center;
   
  }
  
  .signupText .link {
    color: #1e90ff;
    text-decoration: none;
  }
  
  .signupText .link:hover {
    text-decoration: underline;
  }

  